@import "./public-flex.scss";
body, html {
  height: 100%;
  width: 100%;
  position: relative;
}

.header {
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: block;

  .nav {
    position: fixed;
    top: 0;
    @include flex-jc-between;
    padding: 0 100px;
    height: 50px;
    width: calc(100% - 200px);
    z-index: 999;
    transition: .5s;

    &_bgcolor {
      background-color: #0089A7;
    }

    &-logo {
      width: 100px;
      padding: 13px 0;
    }

    &-name {
      font-size: 24px;
      color: #fff;
      font-weight: bold;
      text-shadow: black 0.1em 0.1em 0.2em;
    }

    &-list {
      @include flex-jc-between;
      height: 100%;
      width: 320px;
    }

    &-option {
      margin: 0 20px;
      position: relative;
      cursor: pointer;
      color: #fff;
      text-shadow: black 0.1em 0.1em 0.2em;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: -5px;
        width: 100%;
        height: 1px;
        background-color: #000;
        transform: scalex(0);
        transition: all .3s ease;
      }

      &:hover {
        &::before {
          transform: scalex(1);
        }
      }
    }

    
  }

  .fullscreen {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  }

  .down-box {
    position: absolute;
    bottom: 3%;
    left: 50%;
    z-index: 3;
    width: 40px;
    height: 40px;
    background-image: url(../images/icons/download-white.svg);
    background-position: center;
    background-size: cover;
    transform: translateX(-50%);
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-fill-mode: both;
    animation-name: bounce;
    transform-origin: center bottom;

    .icon {
      width: 30px;
      height: 30px;
      border-radius: 50%;
      padding: 10px;
      border: 3px solid #000;
    }
  }

  .sliders {
    height: 100%;
    background-color: #9e9e9e;
    margin: 0;
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;

    &-option {
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 100%;
      height: inherit;
      overflow: hidden;
      transition: .6s;

      &_show {
        opacity: 2;

        .sliders-textbox {
          left: 200px;
          opacity: 2;
        }
      }

      img {
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
        bottom: 0;
        background-attachment: fixed;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
      }
    }

    .img-1 {
      background-image: url("../images/life/IMG_6218.JPG");
    }

    .img-2 {
      background-image: url("../images/life/IMG_9527.JPG");
    }

    &-textbox {
      position: absolute;
      top: 150px;
      left: 100px;
      opacity: 0;
      transition: .6s;
    }

    &-txt {
      color: #fff;
      font-size: 30px;
      padding-bottom: 10px;
      text-shadow: black 0.1em 0.1em 0.2em;

      &_bold {
        font-size: 40px;
        font-weight: bold;
      }
    }
  }
}

.body {
  width: 100%;
  position: relative;
}

.about {
  padding: 50px 100px;
  width: calc(100% - 200px);
  height: 500px;
  @include flex-c;
  position: relative;

  &_show {
    .about-content {
      left: 100px;
      opacity: 1;
    }

    .about-img {
      opacity: 1;
      right: 100px;
    }
  }

  &-content {
    position: absolute;
    display: block;
    left: -500px;
    opacity: 0;
    transition: .6s;
    width: calc(100% - 500px);

    &-title {
      color: #000;
      font-size: 30px;
      font-weight: bold;
    }

    &-txt {
      padding: 30px;
      line-height: 25px;
    }
  }

  &-img {
    display: block;
    position: absolute;
    right: -500px;
    opacity: 0;
    transition: .6s;

    &:before {
      content: "";
      width: 50px;
      height: 50px;
      position: absolute;
      right: -28px;
      top: -28px;
      border: 6px solid #0089A7;
      opacity: .5;
    }

    &:after {
      content: "";
      width: 50px;
      height: 50px;
      position: absolute;
      left: -28px;
      bottom: -28px;
      border: 6px solid #0089A7;
      opacity: .5;
    }

    img {
      width: 300px;
      height: 400px;
    }
  }
}

.experience {
  padding: 50px 100px;
  width: calc(100% - 200px);
  @include flex-c;
  position: relative;
  height: 1000px;

  &_show {
    .experience-content {
      right: 50px;
      opacity: 1;
    }

    .experience-img {
      opacity: 1;
      left: 100px;
    }
  }

  &-content {
    display: block;
    right: -500px;
    opacity: 0;
    transition: .6s;
    width: calc(100% - 500px);
    position: absolute;

    &-title {
      color: #000;
      font-size: 30px;
      font-weight: bold;

      &_little {
        font-size: 20px;
        line-height: 20px;
        padding: 10px 0;
      }
    }

    &-txt {
      line-height: 25px;
      padding: 10px 10px;
      display: block;

      &_title {
        font-weight: bold;
        font-size: 20px;
        padding: 10px 0;
      }

      &_left {
        padding: 10px 25px;
      }
    }

    &-list {
      padding: 0 20px;
    }
  }

  &-img {
    display: block;
    position: absolute;
    left: -500px;
    opacity: 0;
    transition: .6s;

    &:before {
      content: "";
      width: 50px;
      height: 50px;
      position: absolute;
      right: -28px;
      top: -28px;
      border: 6px solid #0089A7;
      opacity: .5;
    }

    &:after {
      content: "";
      width: 50px;
      height: 50px;
      position: absolute;
      left: -28px;
      bottom: -28px;
      border: 6px solid #0089A7;
      opacity: .5;
    }

    img {
      width: 300px;
      height: 400px;
    }
  }
}

.slider {
  width: 50%;
  margin: 50px auto !important;
}

.slick-slide {
  margin: 0px 20px;
}

.slick-slide img {
  width: 100px;
  height: 100px;
}

.slick-prev:before,
.slick-next:before {
  color: black;
}


.slick-slide {
  transition: all ease-in-out .3s;
  opacity: .2;
}

.slick-active {
  opacity: .5;
}

.slick-current {
  opacity: 1;
}

.footer {
  width: calc(100% - 100px);
  height: 300px;
  background-color: #0089A7;

  padding: 50px 50px;

  &-title {
    font-size: 30px;
    color: #FFFFFB;
  }

  &-list {
    display: block;
    margin-top: 50px;
    @include flex-al-c;
  }

  &-router {
    width: 30px;
    height: 30px;
    margin: 0 30px;

    &-icon1 {
      width: 30px;
      height: 30px;
    }

    &-email {
      @include flex-al-c;

      img {
        padding-right: 10px;
      }
    }

    &-txt {
      font-size: 15px;
      color: #fff;
    }

  &-icon2 {
      width: 30px;
      height: 30px;
      display: none;
    }

    &:hover {
      a>.footer-router-icon1 {
        display: none;
      }

      a>.footer-router-icon2 {
        display: block;
      }
    }
  }
}

